@import url("https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap");
body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #383838;
  font-family: "Poppins", sans-serif;
}

.card {
  position: relative;
  background: #fff;
  width: 400px;
  height: 5 0px;
  margin: 0 auto;
}
.card .content {
  width: 400px;
  padding: 30px;
  box-sizing: border-box;
}
.card .content a {
  display: inline-block;
  margin: 10px 0 0;
  padding: 10px 20px;
  text-decoration: none;
  border: 2px solid #262626;
  color: #262626;
  font-weight: 600;
}
.card .sliderText {
  position: relative;
  width: 100%;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}
.card .sliderText h3 {
  color: #fff;
  font-size: 3em;
}